@import url(https://fonts.googleapis.com/css2?family=Oxygen:wght@400;700&display=swap);
*{margin:0}html{font-size:62.5%}.App{margin:0 auto;max-width:1200px;padding:10px 10px}body{background-image:url(/images/project_papper.png);color:black;font-family:'Oxygen', sans-serif;font-weight:400;font-size:1.4rem}a{text-decoration:none;color:#F0498E}a:hover{text-decoration:underline;color:#555}header{margin-bottom:12px}header div{display:inline-block;margin-left:20px;vertical-align:top}.menu{margin-left:10px}.menu div{background-color:rgba(77,77,77,0.9);color:#fafafa;cursor:pointer;display:inline-block;font-weight:bold;margin:5px 2px;padding:5px 10px 4px;text-decoration:none}.menu div:first-of-type{margin-left:0}.menu div:last-of-type{margin-right:0}.menu div.active,.menu div:hover{color:#f36;background-color:rgba(153,153,153,0.6);text-decoration:none}.filterMenu{margin-bottom:5px}.filterMenu div{border:1px solid grey;cursor:pointer;display:inline-block;margin:0 5px 5px;padding:2px 5px}.filterMenu div.active,.filterMenu div:hover{border-color:#f36;color:#f36}section{margin-bottom:10px}section:first-of-type{margin-top:10px}section header{background:#222;color:#fafafa;font-weight:bold;padding:7px 20px;width:calc(100% - 40px)}section article{padding:20px}section article p{margin:10px 0}section article p:first-of-type{margin-top:0}section article p:last-of-type{margin-bottom:0}section article.news{background:rgba(153,153,153,0.6);text-align:center}section article.news p::after,section article.news p::before{content:'*';font-weight:700;margin:0 3px}blockquote{margin:8px;padding:5px}blockquote p{font-style:italic;margin:0}blockquote p::after,blockquote p::before{content:'"'}blockquote cite{font-style:normal}@media only screen and (max-width: 600px){blockquote:not(:first-of-type){display:none}}.job{background-image:url(/images/000_5.png);border:#fff medium solid;box-sizing:border-box;margin:0;margin-bottom:.6em;padding:10px;width:100%}.job:hover{border:#b7b7b7 medium solid}.job details>summary{cursor:pointer;list-style:none}.job details>summary::-webkit-details-marker{display:none}.job details{margin:0 auto;max-width:800px}.job details summary{outline:none;position:relative;width:100%}.job details summary .thumb{border:#989898 thin solid;display:block;margin:.3rem auto 0;max-width:100%}.job details summary::after{font-family:'Courier New', Courier, monospace;background:#fff;border:#989898 thin solid;color:red;content:'+';font-size:2rem;font-weight:bold;line-height:0;padding:13px 5px 11px;position:absolute;right:-4px;top:-2px}.job details[open] summary::after{content:'-'}.job details section p{padding:4px 0}.job details section p:first-of-type{font-size:1.05em;font-weight:bold}.job details section p.job-award,.job details section p.job-link{padding:1px 0}.job details section p.tech-stack{font-size:1em;font-weight:normal;padding:0}.job .client{font-weight:bold}.job .agency::before{content:'@'}.job .job-date{color:#333;font-size:1.1rem}@media only screen and (min-width: 600px){.job{float:left;width:calc(50% - 6px)}.job.even{margin-right:3px}.job.odd{margin-left:3px;float:right}}section.contact-page{padding:10px 20px}.social{margin:15px 0 7px;width:160px}.social.codepen{background:#000;border-radius:6px;border:0;height:33px;outline:0;padding:0;padding-left:9px}.footer{background:#222;color:#fafafa;font-size:1.2rem;padding:20px 20px;width:calc(100% - 40px)}.clearfix::after{content:"";clear:both;display:table}

